.customArrowBackContainer {
    display: flex;
    align-items: center;
    margin-top: 7px;
    margin-bottom: 10px;
}

.customArrowBack {
    background-color: #ff1a8c;
    height: 35px;
    width: 35px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin-right: 7px;
}

.customArrowBackIcon {
    color: #fff;
    font-size: 25px;
}
